import { RegisterComponent } from './register/register.component';
import { LoginComponent } from './login/login/login.component';
import { AboutComponent } from './about/about.component';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { HomeComponent } from './home/home.component';
import { Error404Component } from './core/error404/error-404.component';
import { BlogDetailComponent } from './blogs/blog-detail/blog-detail.component';
import { BlogListComponent } from './blogs/blog-list/blog-list.component';
import { ProfileComponent } from './profile/profile.component';
import { NewsComponent } from './news/news.component';
import { CreateBlogComponent } from './blogs/create-blog/create-blog.component';
import { EventComponent } from './event/event.component';
import { SettingComponent } from './setting/setting.component';
import { RecepiesComponent } from './recepies/recepies/recepies.component';
import { CreateRecepieComponent } from './recepies/create-recepie/create-recepie.component';
import { RecepieDetailComponent } from './recepies/recepie-detail/recepie-detail.component';
import { FeedbackComponent } from './feedback/feedback.component';

const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'about', component: AboutComponent },
  { path: 'login', component: LoginComponent },
  { path: 'signup', component: RegisterComponent },
  { path: 'blog-list', component: BlogListComponent },
  { path: 'createblog', component: CreateBlogComponent },
  { path: 'blog/:id', component: BlogDetailComponent },
  { path: 'profile', component: ProfileComponent },
  { path: 'news', component: NewsComponent },
  { path: 'event', component: EventComponent },
  { path: 'setting', component: SettingComponent },
  { path: 'recepies', component: RecepiesComponent },
  { path: 'addrecepie', component: CreateRecepieComponent },
  { path: 'recepie/:id', component: RecepieDetailComponent },
  { path: '404', component: Error404Component },
  { path: 'feedback', component: FeedbackComponent },
  { path: '**', redirectTo: '/' + '404' }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [
    RouterModule
  ]
})

export class AppRoutingModule {
}
