import { Component, OnInit, OnDestroy } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Meta, Title } from '@angular/platform-browser';

import { NavigationEnd, Router } from '@angular/router';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MediaChange, MediaObserver } from '@angular/flex-layout';
import { Subscription } from 'rxjs';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent implements OnInit, OnDestroy{

  completeDeviceSize: string;
  deviceSize: string;
  mediaSubscription: Subscription;

  constructor(private translateService: TranslateService,
    private title: Title,
    private meta: Meta,
    private snackBar: MatSnackBar,
    private router: Router,
    public mediaObserver: MediaObserver) {

    this.translateService = translateService;
    this.translateService.setDefaultLang('en');
    this.translateService.use('en');

    this.title.setTitle('Angular10-The WriterHub by Pranay Sanjay Mokal');
    this.router.events.subscribe((event: any) => {
      if (event instanceof NavigationEnd) {
        switch (event.urlAfterRedirects) {
          case '/':
            this.meta.updateTag({
              name: 'description',
              content: 'Angular 10 FullStack WriterHub app by Pranay Sanjay Mokal with Angular Material 10 and NodeJS, MongoDB'
            });
            break;
        }
      }
    });
  }

  ngOnInit() {

    this.mediaSubscription = this.mediaObserver.media$.subscribe((change: MediaChange) => {
      this.completeDeviceSize  = change ? `'${change.mqAlias}' = (${change.mediaQuery})` : '';
      this.deviceSize = this.completeDeviceSize.substr(1, 2);
      console.log(this.deviceSize);
    });
  }

  ngOnDestroy() {
    this.mediaSubscription.unsubscribe();
  }

}
