import { Component, OnInit } from '@angular/core';
import { Recepie } from '../../appcore/models/recepie.model';
import { RecepiesService } from './../../services/recepies.service';
import { SESSION_STORAGE, StorageService } from 'ngx-webstorage-service';
import { Inject, Injectable, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { PageEvent, MatPaginator } from '@angular/material/paginator';


@Component({
  selector: 'app-recepies',
  templateUrl: './recepies.component.html',
  styleUrls: ['./recepies.component.css']
})
export class RecepiesComponent implements OnInit {

  recepies: Recepie[];
  recepieCount: Number = 0;
  length = 0;
  pageIndex = 0;
  pageSize = 5;
  pageEvent: PageEvent;
  pageSizeOptions: number[] = [5, 10, 25, 50, 100];
  @ViewChild(MatPaginator) paginator: MatPaginator;

  constructor(
    private recepieService: RecepiesService,
    @Inject(SESSION_STORAGE) private storageService: StorageService,
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.getAllRecepies();
  }

  ngOnInit() {}

  getAllRecepies(): void {
    this.recepieService.getAllRecepies().subscribe(
      recepieList => {
        this.recepieCount = recepieList.length;
        this.recepies = recepieList;
      },
      error => {
      }
    );
  }

  readRecepie(recepieId: string) {
    this.router.navigate(['/recepie/' + recepieId]);
  }

  pageChange($event) {
    const startIndex = $event.pageIndex * $event.pageSize;
    let endIndex = 0;
    if ($event.previousPageIndex < $event.pageIndex) {
      endIndex = $event.pageSize + $event.pageSize;
    } else {
      endIndex = $event.pageSize;
    }
    this.recepies = this.recepies.slice(startIndex, endIndex);
  }

}
