import { TranslateService } from '@ngx-translate/core';
import { APP_CONFIG } from '../../config/app.config';
import { IAppConfig } from '../../config/iapp.config';
import { Component, Inject, OnInit, Input } from '@angular/core';
import { SESSION_STORAGE, StorageService } from 'ngx-webstorage-service';
import { Router } from '@angular/router';
import { User } from '../../appcore/models/user.model';
import { CommunicatorService } from '../../services/communicator.service';

@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.css']
})
export class NavComponent implements OnInit {
  appConfig: any;
  menuItems: any[];
  progressBarMode: string;
  currentLang: string;
  apptitle: string;
  USER_DATA = 'USER_DATA';
  USER_TOKEN = 'USER_TOKEN';
  isLoggedIn: boolean;
  user: User;
  navItems: any[];
  profilePhoto = '/assets/images/default.png';
  @Input() deviceSize : string;

  constructor(
    @Inject(APP_CONFIG) appConfig: IAppConfig,
    private translateService: TranslateService,
    private router: Router,
    @Inject(SESSION_STORAGE) private storageService: StorageService,
    private communicatorService: CommunicatorService
  ) {
    this.appConfig = appConfig;
    this.translateService = translateService;
    this.loadMenus();
    this.loadNavigationMenus();
    this.currentLang = this.translateService.currentLang;
    this.user = this.storageService.get(this.USER_DATA);
    this.communicatorService.isLoggedIn.subscribe(value => {
      this.isLoggedIn = value;
      this.user = this.storageService.get(this.USER_DATA);
    });
  }

  ngOnInit() {
    console.log("nav device size",this.deviceSize)
  }

  changeLanguage(language: string): void {
    this.translateService.use(language).subscribe(() => {
      this.loadMenus();
      this.loadNavigationMenus();
    });
  }

  private loadMenus(): void {
    this.translateService
      .get(['about', 'blogs', 'recepies'], {})
      .subscribe((texts: any) => {
        this.menuItems = [
          { link: '/blog-list', name: texts['blogs'] },
          { link: '/recepies', name: texts['recepies'] },
          { link: '/about', name: texts['about'] }
        ];
      });
  }

  private loadNavigationMenus(): void {
    this.translateService
      .get(
        [
          'blogs',
          'recepies',
          'feedback'
        ],
        {}
      )
      .subscribe((texts: any) => {
        this.navItems = [
          { link: '/blog-list', name: texts['blogs'] },
          { link: '/recepies', name: texts['recepies'] },
          { link: '/feedback', name: texts['feedback'] },
        ];
      });
  }

  logout() {
    this.storageService.remove(this.USER_TOKEN);
    this.storageService.remove(this.USER_DATA);
    this.communicatorService.isLoggedIn.next(false);
    this.router.navigate(['']);
  }
}
