import { Component, OnInit } from '@angular/core';

import { User } from '../appcore/models/user.model';
import { UserService } from '../services/user.service';
import { Blog } from '../../app/appcore/models/blog.model';
import { SESSION_STORAGE, StorageService } from 'ngx-webstorage-service';
import { Inject } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { BlogsService } from '../services/blogs.service';

@Component({
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  currentUser: User;
  users: User[] = [];
  blogs: Blog[] = null;
  blogCount: number;

  constructor(
    private userService: UserService,
    @Inject(SESSION_STORAGE) private storageService: StorageService,
    private route: ActivatedRoute,
    private router: Router, private blogService: BlogsService) {

  }

  ngOnInit() {

    this.getAllBlogs();
  }

  getAllBlogs(): void {
    this.blogService.getAllblogs().subscribe(
      blogList => {
        this.blogCount = blogList.length;
        this.blogs = blogList;
      },
      error => {
      }
    );
  }

  readBlog(_id: string) {
    this.router.navigate(['/blog/' + _id]);
  }
}
