import { map } from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { AppConfig } from '../config/app.config';
import { throwError as observableThrowError, Observable, of } from 'rxjs';
import { Injectable } from '@angular/core';
import { Feedback } from '../appcore/models/feedback.model';
import { MatSnackBar, MatSnackBarConfig } from '@angular/material/snack-bar';

@Injectable()
export class FeedbackService {
  headers: HttpHeaders;
  constructor(private http: HttpClient, private snackBar: MatSnackBar) { }

  private handleError(error: any) {
    if (error instanceof Response) {
      return observableThrowError(error.json()['error'] || 'backend server error');
    }
    return observableThrowError(error || 'backend server error');
  }

  getAllFeedbacks(): Observable<Feedback[]> {
    return this.http.get<Feedback[]>(AppConfig.appRestPoints.feedbacks).pipe(
      map(response => response));
  }

  getFeedbackById(feedbackId: string): Observable<Feedback> {
    return this.http.get<Feedback>(AppConfig.appRestPoints.feedbacks + '/' + feedbackId)
      .pipe(map(response => response));
  }

  sendFeedback(feedback: any): Observable<Feedback> {
    this.headers = new HttpHeaders({ 'Content-Type': 'application/json' });
    try {
      return this.http
        .post<Feedback>(AppConfig.appRestPoints.feedbacks + '/create', JSON.stringify({
          senderName: feedback.senderName,
          senderEmail: feedback.senderEmail,
          senderFeedback: feedback.senderFeedback,
        }), { headers: this.headers }).pipe(
          map(response => {
            this.showSnackBar('Feedback sent sucessfully');
            return response;
          }));
    } catch (error) {
      this.handleError(error);
    }
  }

  showSnackBar(name): void {
    const config: any = new MatSnackBarConfig();
    config.duration = AppConfig.snackBarDuration;
    this.snackBar.open(name, 'OK', config);
  }


}
