import { Dislike } from './../appcore/models/dislike.model';
import { Like } from './../appcore/models/like.model';
import { map } from 'rxjs/operators';
import { TranslateService } from '@ngx-translate/core';
import { MatSnackBar, MatSnackBarConfig } from '@angular/material/snack-bar';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { AppConfig } from '../config/app.config';
import { throwError as observableThrowError, Observable, of } from 'rxjs';
import { Blog } from '../../app/appcore/models/blog.model';
import { SESSION_STORAGE, StorageService } from 'ngx-webstorage-service';
import { Inject, Injectable } from '@angular/core';
import { Comment } from '../../app/appcore/models/comment.model';

@Injectable()
export class BlogsService {
  private headers: HttpHeaders;
  private translations: any;
  private blogs: Blog[];
  private blog: Blog;
  private token: string;
  private secureheaders: HttpHeaders;

  private handleError(error: any) {
    if (error instanceof Response) {
      return observableThrowError(error.json()['error'] || 'backend server error');
    }
    return observableThrowError(error || 'backend server error');
  }

  constructor(private http: HttpClient,
    private translateService: TranslateService,
    private snackBar: MatSnackBar,
    @Inject(SESSION_STORAGE) private storageService: StorageService) {
    this.headers = new HttpHeaders({ 'Content-Type': 'application/json'/*, 'x-access-token': this.token*/ });
  }

  getAllblogs(): Observable<Blog[]> {
    return this.http.get<Blog[]>(AppConfig.appRestPoints.blogs, { headers: this.headers }).pipe(
      map(response => response));
  }

  getBlogById(blogId: string): Observable<Blog> {
    return this.http.get<Blog>(AppConfig.appRestPoints.blogs + '/' + blogId, { headers: this.headers })
      .pipe(map(response => response));
  }

  getCommentsByBlogId(blogId: string): Observable<Comment[]> {
    return this.http.get<Comment[]>(AppConfig.appRestPoints.comments + '/blog/' + blogId, { headers: this.headers })
      .pipe(map(response => response));
  }

  like(blogId: string, isLiked: boolean): Observable<Like> {
    this.token = this.storageService.get('USER_TOKEN');
    if (!this.token) {
      return of(new Like(0, 'please sign in to like')
      );
    }
    this.headers = new HttpHeaders({ 'Content-Type': 'application/json', 'x-access-token': this.token });
    try {
      return this.http
        .post<Like>(AppConfig.appRestPoints.blogs + '/' + blogId + '/like', JSON.stringify({ isLiked: isLiked }), { headers: this.headers }).pipe(
          map(response => {
            return response;
          }));
    } catch (error) {
      this.handleError(error);
    }
  }

  dislike(blogId: string, disLiked: boolean): Observable<Dislike> {
    this.token = this.storageService.get('USER_TOKEN');
    if (!this.token) {
      return of(new Dislike(0, 'please sign in to Dislike'));
    }
    this.headers = new HttpHeaders({ 'Content-Type': 'application/json', 'x-access-token': this.token });
    try {
      return this.http
        .post<Dislike>(AppConfig.appRestPoints.blogs + '/' + blogId + '/dislike', JSON.stringify({ isDisLiked: disLiked }), { headers: this.headers }).pipe(
          map(response => {
            return response;
          }));
    } catch (error) {
      this.handleError(error);
    }
  }

  createBlog(blog: any): Observable<Blog> {
    this.token = this.storageService.get('USER_TOKEN');
    this.headers = new HttpHeaders({ 'Content-Type': 'application/json', 'x-access-token': this.token });
    try {
      return this.http
        .post<Blog>(AppConfig.appRestPoints.blogs + '/create', JSON.stringify({
          blogName: blog.blogName,
          blogContent: blog.blogContent,
          blogCategory: blog.blogCategory,
          blogAuthor: blog.blogAuthor,
          blogTags: [blog.blogTags],
          blogImage: blog.blogImage
        }), { headers: this.headers }).pipe(
          map(response => {
            this.showSnackBar('Blog Created Sucessfully');
            return response;
          }));
    } catch (error) {
      this.handleError(error);
    }
  }

  addCommentOnBlog(comment: Comment): Observable<string> {
    this.token = this.storageService.get('USER_TOKEN');
    if (!this.token) {
      return of('please sign in to Comment');
    }
    this.headers = new HttpHeaders({ 'Content-Type': 'application/json', 'x-access-token': this.token });
    try {
      return this.http
        .post<string>(AppConfig.appRestPoints.comments + '/create', JSON.stringify({
          commentText: comment.commentText,
          commentAuthor: comment.commentAuthor,
          blogId: comment.blogId,
        }), { headers: this.headers }).pipe(
          map(response => {
            return response;
          }));
    } catch (error) {
      this.handleError(error);
    }
  }

  showSnackBar(name): void {
    const config: any = new MatSnackBarConfig();
    config.duration = AppConfig.snackBarDuration;
    this.snackBar.open(this.translations[name], 'OK', config);
  }
}
