
import { map } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpResponse } from '@angular/common/http';
import { throwError as observableThrowError, Observable, of } from 'rxjs';
import { News } from './../appcore/models/news.model';
import { AppConfig } from '../config/app.config';


@Injectable()
export class NewsService {
  private headers: HttpHeaders;
  private newsUrl: string;
  private newsUpdated: string;
  constructor(private http: HttpClient) {
    this.newsUrl = AppConfig.appRestPoints.news;
    this.headers = new HttpHeaders({ 'Content-Type': 'application/json' });
  }

  getAllNews(country: String, category: String): Observable<News[]> {
    try {
      if (country) {
        this.newsUpdated = this.newsUrl + 'in' + AppConfig.appRestPoints.newsapi;
      }
      else if (category) {
        this.newsUpdated = this.newsUrl + country + '&category=' + category + AppConfig.appRestPoints.newsapi;
      } else {
        this.newsUpdated = this.newsUrl + country + '&category=' + category + AppConfig.appRestPoints.newsapi;
      }
      return this.http.get<{ articles: News[] }>(this.newsUpdated).pipe(
        map(response => response.articles));
    } catch (error) {
      this.handleError(error);
    }
  }


  getAllNewsBySource(source: String): Observable<News[]> {
    try {

      this.newsUpdated = 'https://newsapi.org/v2/top-headlines?sources=' + source + AppConfig.appRestPoints.newsapi;
      return this.http.get<{ articles: News[] }>(this.newsUpdated).pipe(
        map(response => response.articles));
    } catch (error) {
      this.handleError(error);
    }
  }

  private handleError(error: any) {
    return observableThrowError(error || 'backend server error');
  }
}
