
import { map } from 'rxjs/operators';
import { HttpHeaders } from '@angular/common/http';
import { RequestOptions, Http, Headers, Response } from '@angular/http';
import { User } from '../appcore/models/user.model';
import { SESSION_STORAGE, StorageService } from 'ngx-webstorage-service';
import { Inject, Injectable } from '@angular/core';
import { AppConfig } from '../config/app.config';


@Injectable()
export class UserService {
    private headers: HttpHeaders;
    private user_key = 'USER_DATA';
    private storedUser: User;
    constructor(private http: Http, @Inject(SESSION_STORAGE) private storageService: StorageService) {
    }

    getAll() {
        return this.http.get(AppConfig.appRestPoints.users).pipe(map((response: Response) => response));
    }

    getById(id: string) {
        return this.http.get(AppConfig.appRestPoints.users + '/' + id).pipe(map((response: Response) => response));
    }

    login(user: User) {
        return this.http.post(AppConfig.appRestPoints.login, user).pipe(map((response: Response) => response));
    }

    create(user: User) {
        return this.http.post(AppConfig.appRestPoints.register, user).pipe(map((response: Response) => response));
    }

    update(user: User) {
        return this.http.put(AppConfig.appRestPoints.users + '/' + user._id, user).pipe(map((response: Response) => response));
    }

    delete(id: string) {
        return this.http.delete(AppConfig.appRestPoints.users + '/' + id).pipe(map((response: Response) => response));
    }
}
