import { Component, OnInit } from '@angular/core';
import { BlogsService } from './../../services/blogs.service';
import { Blog } from '../../../app/appcore/models/blog.model';
import { SESSION_STORAGE, StorageService } from 'ngx-webstorage-service';
import { Inject, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { PageEvent, MatPaginator } from '@angular/material/paginator';

@Component({
  selector: 'app-blog-list',
  templateUrl: './blog-list.component.html',
  styleUrls: ['./blog-list.component.css']
})
export class BlogListComponent implements OnInit {
  blogs: Blog[];
  blogCount: Number = 0;
  length = 0;
  pageIndex = 0;
  pageSize = 6;
  pageEvent: PageEvent;
  pageSizeOptions: number[] = [6, 12, 18, 24, 30];
  @ViewChild(MatPaginator) paginator: MatPaginator;

  constructor(
    private blogService: BlogsService,
    @Inject(SESSION_STORAGE) private storageService: StorageService,
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.getAllBlogs();
  }

  ngOnInit() { }

  getAllBlogs(): void {
    this.blogService.getAllblogs().subscribe(
      blogList => {
        this.blogCount = blogList.length;
        this.blogs = blogList;
      },
      error => {
      }
    );
  }

  readBlog(blogId: string) {
    this.router.navigate(['/blog/' + blogId]);
  }

  pageChange($event) {
    const startIndex = $event.pageIndex * $event.pageSize;
    let endIndex = 0;
    if ($event.previousPageIndex < $event.pageIndex) {
      endIndex = $event.pageSize + $event.pageSize;
    } else {
      endIndex = $event.pageSize;
    }
    this.blogs = this.blogs.slice(startIndex, endIndex);
  }
}
