import { Component, OnInit } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { EventService } from './../services/event.service';
import { AlertService } from './../services/alert.service';
import { MyEvent } from '../appcore/models/myevent.model';
import { Router } from '@angular/router';

@Component({
  selector: 'app-event',
  templateUrl: './event.component.html',
  styleUrls: ['./event.component.css']
})
export class EventComponent implements OnInit {
  eventRegisterationForm: FormGroup;
  events: MyEvent[];
  event: MyEvent;
  error: any;
  imageSrc = '';

  constructor(private formBuilder: FormBuilder, private eventService: EventService, private alertService: AlertService,
    private router: Router) {
    this.eventRegisterationForm = this.formBuilder.group({
      'eventName': ['', [Validators.required]],
      'eventDescription': ['', [Validators.required]],
      'eventStartDate': ['', [Validators.required]],
      'eventEndDate': ['', [Validators.required]],
      'eventLocation': ['', [Validators.required]],
      'eventImage': [],
    });
  }

  ngOnInit() {
    this.getAllEvents();
  }

  registerEvent(eventRegisterationForm) {
    eventRegisterationForm.eventImage = this.imageSrc;
    this.eventService.createMyEvent(eventRegisterationForm).subscribe((events) => {
      this.router.navigate(['/event']);
    }, (response) => {
      if (response.status === 500) {
        this.error = 'Failed to create event';
      }
      if (response.status === 403) {
        this.router.navigate(['/login']);
      }
      if (response.status === 201) {
        this.router.navigate(['/event']);
      }
    });
    this.getAllEvents();
  }

  getAllEvents() {
    this.eventService.getAllMyEvents().subscribe(eventList => {
      this.events = eventList;
    });
  }

  getEventById(eventId: string) {

  }

  handleInputChange(e) {
    const file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    const pattern = /image-*/;
    const reader = new FileReader();
    if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
    }
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }

  _handleReaderLoaded(e) {
    const reader = e.target;
    this.imageSrc = reader.result;
  }
}
