import { Component, OnInit } from '@angular/core';
import { Recepie } from '../../appcore/models/recepie.model';
import { Router, ActivatedRoute } from '@angular/router';
import { RecepiesService } from './../../services/recepies.service';
import { throwError as observableThrowError, Observable, of } from 'rxjs';

@Component({
  selector: 'app-recepie-detail',
  templateUrl: './recepie-detail.component.html',
  styleUrls: ['./recepie-detail.component.css']
})
export class RecepieDetailComponent implements OnInit {
  recepie: Recepie;
  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private recepieService: RecepiesService
  ) {}

  ngOnInit() {
    const recepieDetail = this.recepieService.getRecepieById(
      this.route.snapshot.paramMap.get('id')
    );
    recepieDetail.subscribe(res => {
      this.recepie = res;
    });
  }

}
