import { Component, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { BlogsService } from './../../services/blogs.service';
import { Router } from '@angular/router';
import { SESSION_STORAGE, StorageService } from 'ngx-webstorage-service';
import { Inject } from '@angular/core';
import { User } from '../../appcore/models/user.model';

@Component({
  selector: 'app-create-blog',
  templateUrl: './create-blog.component.html',
  styleUrls: ['./create-blog.component.css']
})
export class CreateBlogComponent {
  createBlog: FormGroup;
  @ViewChild('form') ngForm;
  error: string;
  imageSrc = '';
  user: User;
  model = {
    blogName: '',
    blogState: '',
    blogImage: '',
    blogCategory: '',
    blogContent: '',
    blogTags: '',
    blogTemplate: ''
  };

  categories = [
    { value: 'education', viewValue: 'Education' },
    { value: 'history', viewValue: 'History' },
    { value: 'science', viewValue: 'Science' },
    { value: 'economic', viewValue: 'Economics' },
    { value: 'health', viewValue: 'Health' },
    { value: 'news', viewValue: 'News' },
    { value: 'entertainment', viewValue: 'Entertainment' },
    { value: 'other', viewValue: 'Other' }
  ];
  templates = [
    { value: 'select', viewValue: 'Select' },
    { value: 'grid', viewValue: 'Grid Template' },
    { value: 'side', viewValue: 'Side Template' },
    { value: 'expandable', viewValue: 'Expandable Template' }
  ];

  constructor(private blogsService: BlogsService,
    private formBuilder: FormBuilder, private router: Router, @Inject(SESSION_STORAGE) private storageService: StorageService) {
    this.createBlog = this.formBuilder.group({
      'blogName': ['', [Validators.required]],
      'blogContent': ['', [Validators.required]],
      'blogTemplate': ['', [Validators.required]],
      'blogTags': ['', Validators.required],
      'blogCategory': ['', Validators.required],
      'blogImage': [''],
    });
  }

  createNewBlog(newBlog) {
    newBlog.blogImage = this.imageSrc;
    newBlog.blogState = 'created';
    this.user = this.storageService.get('USER_DATA');
    newBlog.blogAuthor = this.user.firstName + ' ' + this.user.lastName;
    this.blogsService.createBlog(newBlog).subscribe((blogs) => {
      this.router.navigate(['/blog-list']);
    }, (response) => {
      if (response.status === 500) {
        this.error = 'errorHasOcurred';
      }
      if (response.status === 201) {
        this.router.navigate(['/blog-list']);
      }
    });
  }

  publishBlog(model) {
    model.blogImage = this.imageSrc;
    model.blogState = 'published';
    this.blogsService.createBlog(model).subscribe((blogs) => {
      this.router.navigate(['/blog-list']);
    }, (response) => {
      if (response.status === 500) {
        this.error = 'errorHasOcurred';
      }
      if (response.status === 201) {
        this.router.navigate(['/blog-list']);
      }
    });
  }

  handleInputChange(e) {
    const file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    const pattern = /image-*/;
    const reader = new FileReader();
    if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
    }
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }

  _handleReaderLoaded(e) {
    const reader = e.target;
    this.imageSrc = reader.result;
  }
}
