import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { SESSION_STORAGE, StorageService } from 'ngx-webstorage-service';
import { Inject, Injectable } from '@angular/core';
import { User } from '../appcore/models/user.model';
@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  sharePhoto: FormGroup;
  error: string;
  USER_DATA = 'USER_DATA';
  user: User;
  imageSrc = 'assets/images/default.png';
  updatedImage = '';
  constructor(private formBuilder: FormBuilder, @Inject(SESSION_STORAGE) private storageService: StorageService
  ) {
    this.sharePhoto = this.formBuilder.group({
      'photo_path': ['', [Validators.required]],
      'photo_caption': ['', [Validators.required]],
      'photo_location': ['', [Validators.required]],
      'upload_date': ['', Validators.required],
    });
    this.user = this.storageService.get(this.USER_DATA);
  }
  ngOnInit() {
    this.user = this.storageService.get(this.USER_DATA);
  }
  uploadImage(updatedImage) {
    console.log(updatedImage);
  }

  handleInputChange(e) {
    const file = e.dataTransfer ? e.dataTransfer.files[0] : e.target.files[0];
    const pattern = /image-*/;
    const reader = new FileReader();
    if (!file.type.match(pattern)) {
      alert('invalid format');
      return;
    }
    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }

  _handleReaderLoaded(e) {
    const reader = e.target;
    this.imageSrc = reader.result;
  }
}
