import { Component, OnInit } from '@angular/core';
import { NewsService } from './../services/news.service';
import { News } from './../appcore/models/news.model';
@Component({
  selector: 'app-news',
  templateUrl: './news.component.html',
  styleUrls: ['./news.component.css']
})
export class NewsComponent implements OnInit {
  news: News[] = [];

  news_categories = [
    { value: 'business', viewValue: 'Business' },
    { value: 'technology', viewValue: 'Technology' },
    { value: 'entertainment', viewValue: 'Entertainment' },
    { value: 'health', viewValue: 'Health' },
    { value: 'science', viewValue: 'Science' },
    { value: 'sports', viewValue: 'Sports' }
  ];
  newsources = [{ value: 'the-times-of-india', viewValue: 'Times of India' }, { value: 'google-news-in', viewValue: 'Google News' },
  { value: 'the-hindu', viewValue: 'The Hindu' }, { value: 'cnn', viewValue: 'CNN' }];

  countries = [
    { value: 'in', viewValue: 'India' },
    { value: 'au', viewValue: 'Australia' },
    { value: 'ar', viewValue: 'Austria' },
    { value: 'br', viewValue: 'Brazil' },
    { value: 'cn', viewValue: 'China' },
    { value: 'in', viewValue: 'India' },
    { value: 'id', viewValue: 'Indonation' },
    { value: 'fr', viewValue: 'France' },
    { value: 'de', viewValue: 'Germany' },
    { value: 'it', viewValue: 'Italy' },
    { value: 'uk', viewValue: 'United Kingdom' },
    { value: 'us', viewValue: 'United States' }
  ];
  selectedCountry = '';
  selectedCategory = '';
  selectedSource = '';
  selectedCntry = 'in';
  selectedCat = 'business';
  
  constructor(private newsService: NewsService) {
    this.news = null;
    this.newsService.getAllNews('in', 'business').subscribe(
      data => {
        this.news = data;
      }, err => {
      }
    );
  }

  selectOneCountry(selected) {
    this.selectedCountry = selected.value;
    this.news = null;
    this.newsService.getAllNews(this.selectedCountry, 'business').subscribe(
      data => {
        this.news = data;
      }, err => {
      }
    );
  }

  selectOneCategory(selected) {
    this.selectedCategory = selected.value;
    this.news = null;
    this.newsService.getAllNews(this.selectedCountry, this.selectedCategory).subscribe(
      data => {
        this.news = data;
      }, err => {
      }
    );
  }


  selectOneSource(selected) {
    this.selectedSource = selected.value;
    this.news = null;
    this.newsService.getAllNewsBySource(this.selectedSource).subscribe(
      data => {
        this.news = data;
      }, err => {
      }
    );
  }
  ngOnInit() {
  }

}
